import React, { useState, useEffect } from "react";
import {
    OverlayTrigger, Tooltip, Popover, InputGroup, ListGroup, FormControl,
    Container, Row, Col, Card, Accordion, Button, ButtonGroup, ToggleButton
} from "react-bootstrap";
import { LangModule, LangModuleMask } from "./LangModule";
import "./App.scss";

// Default source for language module data
const DEFAULT_SOURCE = "https://raw.githubusercontent.com/nilsso/nilsso.github.io/code/static/apps/lang_modules/japanese_modules.json";

// Helper functions
const mask = (length: number, fill: boolean) => Array(length).fill(fill);
const toggle_mask = (mask: Array<boolean>, i: number, value: boolean) => {
    let res = [...mask];
    res[i] = value;
    return res;
};

// Fetch ("GET" request) file from URL and parse a JSON
async function requestJSON(url: string) {
    var module_data;
    await fetch(url)
        .then(response => response.json())
        .then(data => module_data = data)
        .catch(e => alert("Invalid source URL\n" + e));
    if (module_data) {
        return module_data;
    } else {
        return [];
    }
}

function App() {
    // Module source
    const [source, setSource] = useState(DEFAULT_SOURCE);
    // Modules
    const [modules, setModules] = useState(Array<LangModule>(0));
    // Joined module
    const [joined, setJoined] = useState(LangModule.empty());
    // Module masks
    const [enabledMask, setEnabledMask] = useState(mask(0, false));
    const [fragmentMask, setFragmentMask] = useState(mask(0, true));
    const [vocabularyMask, setVocabularyMask] = useState(mask(0, true));

    // If module source changed build new modules
    useEffect(() => {
        requestJSON(source).then(data => setModules(data.map(json => LangModule.from_json(json))));
    }, [source]);

    // If modules changed update the module masks
    useEffect(() => {
        setEnabledMask(mask(modules.length, false));
        setFragmentMask(mask(modules.length, true));
        setVocabularyMask(mask(modules.length, true));
    }, [modules]);

    // If module masks changed update the joined module
    useEffect(() => {
        if (modules.length > 0) {
            setJoined(LangModule.join(modules, new LangModuleMask(enabledMask, fragmentMask, vocabularyMask)));
        }
    }, [modules, enabledMask, fragmentMask, vocabularyMask]);

    return (
        <Container>
            <Row>
                <GeneratorComponent joined={joined} enabledMask={enabledMask}/>
                <ModulesComponent
                    modules={modules}

                    enabledMask={enabledMask}
                    setEnabledMask={setEnabledMask}

                    fragmentMask={fragmentMask}
                    setFragmentMask={setFragmentMask}

                    vocabularyMask={vocabularyMask}
                    setVocabularyMask={setVocabularyMask}
                />
            </Row>
            <Source source={source} setSource={setSource}/>
        </Container>
    );
}

function Source(props: any) {
    const [source, setSource] = [props.source, props.setSource];

    const [value, setValue] = useState(source);

    return (
        <Row>
            <Col className="component">
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            Module Source
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        defaultValue={value}
                        onChange={e => setValue(e.target.value)}
                    />
                    <InputGroup.Append>
                        <Button
                            variant="outline-secondary"
                            onClick={() => setSource(value)}
                        >
                            Load
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </Col>
        </Row>
    );
}

function GeneratorComponent(props: any) {
    const joined = props.joined;

    const enabledMask = props.enabledMask;

    const [generated, setGenerated] = useState("...");
    const [meaning, setMeaning] = useState("");
    const [hidden, setHidden] = useState("...");
    const [swap, setSwap] = useState(false);

    const some_enabled = () => enabledMask.some(e => e);

    const help = (
        <Popover
            id="help-popover"
        >
            <Popover.Title as="h3">Language Fragment Generator</Popover.Title>
            <Popover.Content>
                <p>
                    Language modules are like individual lessons of a language class, where each module contains a list of <strong>fragments</strong> and <strong>vocabulary</strong>. Under the card title <strong>Modules</strong> is the list of available language modules, each with an <span className="text-primary">Enable</span> button to enable the module, a <span className="text-secondary">Fragments</span> button to enable the fragments of that module, and a <span className="text-secondary">Vocabulary</span> button to enable the vocabulary of that module. The <span className="text-primary">All</span> button will enable all of the modules, and the <span className="text-danger">None</span> button will disable them all.
                </p>
                <p>
                    Fragments can be entire sentences, or can contain <strong>placeholders</strong> that will be substituted upon generation. For example, a fragment containing the <code>{"{{noun}}"}</code> placeholder would have a random noun inserted where the placeholder is.
                </p>
                <p>
                    With one or more modules <strong>enabled</strong>, clicking the <span className="text-primary">Generate</span> button generates a <strong>populated</strong> fragment by first randomly selecting a fragment from any of the modules with fragments enabled, and then populating the fragment with randomly selected vocabulary from any of the language modules with vocabulary enabled.
                </p>
                <p>
                    Clicking the <span className="text-success">Reveal</span> button will show the meaning of what was generated, and toggling on the <span className="text-secondary">Swap</span> button will instead first display the meaning so that revealing will show the populated fragment second.
                </p>
            </Popover.Content>
        </Popover>
    );

    const disabled = (
        <Tooltip
            id="generator-buttons-tooltip"
            className="tooltip-danger"
        >
            Enable at least one module
        </Tooltip>
    );

    return (
        <Col className="component">
            <Card>
                <Card.Header>
                    <span>Generator</span>
                    <OverlayTrigger trigger="click" placement="bottom" overlay={help}>
                        <Button variant="info">Help</Button>
                    </OverlayTrigger>
                </Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <p>{generated}</p>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <p>{hidden}</p>
                    </ListGroup.Item>
                    <OverlayTrigger
                        placement="top"
                        show={!some_enabled()}
                        overlay={disabled}
                    >
                        <ButtonGroup id="generator-buttons">
                            <Button
                                disabled={!some_enabled()}
                                onClick={() => {
                                    let [generated, meaning] = joined.random();
                                    if (generated) {
                                        [generated, meaning] = swap ? [meaning, generated] : [generated, meaning];
                                        setHidden("...");
                                        setGenerated(generated);
                                        setMeaning(meaning);
                                    }
                                }
                                }
                            >
                                Generate
                            </Button>
                            <Button
                                variant="success"
                                disabled={!some_enabled()}
                                onClick={() => setHidden(meaning)}
                            >
                                Reveal
                            </Button>
                            <Button
                                active={swap}
                                variant="outline-secondary"
                                disabled={!some_enabled()}
                                onClick={() => setSwap(!swap)}
                            >
                                Swap
                            </Button>
                        </ButtonGroup>
                    </OverlayTrigger>
                </ListGroup>
            </Card>
        </Col>
    );
}

function ModulesComponent(props: any) {
    // Modules state
    const modules = props.modules;
    // Mask states
    const [enabledMask, setEnabledMask] = [props.enabledMask, props.setEnabledMask];
    const [fragmentMask, setFragmentMask] = [props.fragmentMask, props.setFragmentMask];
    const [vocabularyMask, setVocabularyMask] = [props.vocabularyMask, props.setVocabularyMask];

    // Helper to toggle all modules
    const toggleAll = function(checked: boolean) {
        setEnabledMask(mask(modules.length, checked));
    }

    let module_components = modules.map((lang_module: LangModule, i: number) => {
        return <LangModuleComponent
            key={i}
            lang_module={lang_module}

            enabledFlag={enabledMask[i]}
            fragmentFlag={fragmentMask[i]}
            vocabularyFlag={vocabularyMask[i]}

            setEnabledFlag={(checked: boolean) => setEnabledMask(toggle_mask(enabledMask, i, checked))}
            setFragmentFlag={(checked: boolean) => setFragmentMask(toggle_mask(fragmentMask, i, checked))}
            setVocabularyFlag={(checked: boolean) => setVocabularyMask(toggle_mask(vocabularyMask, i, checked))}
        />;
    });

    return (
        <Col className="component">
            <Card>
                <Card.Header>
                    <span>Modules</span>
                    <Button
                        variant="primary"
                        onClick={() => toggleAll(true)}
                    >
                        All
                    </Button>
                    <Button
                        variant="danger"
                        onClick={() => toggleAll(false)}
                    >
                        None
                    </Button>
                </Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <div id="module-list">{module_components}</div>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    );
}

function LangModuleComponent(props: any) {
    // Modules state
    const lang_module = props.lang_module;
    // Mask states
    const [enabledFlag, setEnabledFlag] = [props.enabledFlag, props.setEnabledFlag];
    const [fragmentFlag, setFragmentFlag] = [props.fragmentFlag, props.setFragmentFlag];
    const [vocabularyFlag, setVocabularyFlag] = [props.vocabularyFlag, props.setVocabularyFlag];

    const has_fragments = !!fragmentFlag;
    const has_vocabulary = !!vocabularyFlag;

    return(
        <Accordion className="module">
            <Card>
                <Card.Header>
                    {lang_module.name}
                </Card.Header>
                <Card.Body id="module-control">
                    <ButtonGroup
                        toggle className="module-controls"
                    >
                        <ToggleButton
                            type="checkbox"
                            variant="outline-primary"
                            size="sm"
                            value={0}
                            checked={enabledFlag}
                            onChange={(e) => setEnabledFlag(e.currentTarget.checked)}
                        >
                            Enable
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            size="sm"
                            value={1}
                            checked={fragmentFlag}
                            disabled={has_fragments && !enabledFlag}
                            onChange={(e) => setFragmentFlag(e.currentTarget.checked)}
                        >
                            Fragments
                        </ToggleButton>
                        <ToggleButton
                            type="checkbox"
                            variant="outline-secondary"
                            size="sm"
                            value={2}
                            checked={vocabularyFlag}
                            disabled={has_vocabulary && !enabledFlag}
                            onChange={(e) => setVocabularyFlag(e.currentTarget.checked)}
                        >
                            Vocabulary
                        </ToggleButton>
                    </ButtonGroup>
                </Card.Body>
            </Card>
            <Card>
                <FragmentListComponent lang_module={lang_module} />
                <VocabularyListComponent lang_module={lang_module} />
            </Card>
        </Accordion>
    );
}

function FragmentListComponent(props: any) {
    const lang_module = props.lang_module;

    const rows = lang_module.fragments.map((s, i) => {
        return (
            <Row key={i} className="module-content-item">
                <Col>{s.fragment_template}</Col>
                <Col>{s.meaning_template}</Col>
            </Row>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="0"
            >
                View Fragments
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
                <Card.Body>
                    <Card className="module-content">
                        <Container fluid>
                            {rows}
                        </Container>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

function VocabularyListComponent(props: any) {
    const lang_module = props.lang_module;

    const token_rows = function(choices) {
        return choices.map(([vocab, meaning], i) => {
            return (
                <Row key={i} className="module-content-item">
                    <Col>{vocab}</Col>
                    <Col>{meaning}</Col>
                </Row>
            );
        });
    }

    const vocabulary_rows = Object.entries(lang_module.vocabulary).map(([token, choices], i) => {
        return (
            <React.Fragment key={i}>
                <ListGroup.Item variant="secondary">{token}</ListGroup.Item>
                <ListGroup.Item className="vocabulary">
                    <Container>
                        {token_rows(choices)}
                    </Container>
                </ListGroup.Item>
            </React.Fragment>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="1"
            >
                View Vocabulary
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="1">
                <Card.Body>
                    <Card className="module-content">
                        <ListGroup variant="flush">
                            {vocabulary_rows}
                        </ListGroup>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

export default App;
